<script setup lang="ts">
import HeaderDesktop from '~/components/header/HeaderDesktop.vue'
import HeaderMobile from '~/components/header/HeaderMobile.vue'
import { useLocalePath, useSwitchLocalePath } from '#i18n'

/* Stores & i18n */
const cart = (typeof useCart === 'function' ? useCart() : null) as any

// Keep i18n instance
const i18n = useI18n()
const { t, locale } = i18n
const localePath = useLocalePath()
const switchLocalePath = useSwitchLocalePath()

/* Safe locales accessor (SSRx/edge environments may not expose it yet) */
const safeLocales = computed<any[]>(() => {
  const raw = (i18n as any)?.locales
  const arr = Array.isArray(raw?.value) ? raw.value : (Array.isArray(raw) ? raw : null)
  return arr ?? [{ code: 'en', dir: 'ltr' }]
})

/* ---- Locale helpers (WORK WITH STRING OR REF) ---- */

// Normalized current locale code (always string)
const localeCode = computed<string>(() => {
  const l: any = locale
  if (l && typeof l === 'object' && 'value' in l) {
    return l.value
  }
  return String(l)
})

// Generic setter that works for both ref and non-ref locale
function setLocale(code: string) {
  const l: any = locale
  if (l && typeof l === 'object' && 'value' in l) {
    l.value = code
  } else if ((i18n as any).setLocale) {
    ;(i18n as any).setLocale(code)
  } else {
    ;(i18n as any).locale = code
  }
}

/* Auth */
const auth = (typeof useAuth === 'function' ? useAuth() : null) as any
const user = computed(() => (auth && auth.user ? auth.user.value : null))
const isLoggedIn = computed(() => {
  const token = process.client
    ? (localStorage.getItem('auth_token') || localStorage.getItem('pk:token'))
    : null
  return !!token || !!user.value
})

const displayName = computed(() => {
  const u: any = user.value
  if (!u) return ''
  const first = u.first_name ?? u.firstName
  const last  = u.last_name  ?? u.lastName
  const full  = (u.name || [first, last].filter(Boolean).join(' ')).trim()
  return full || (String(u.email || '').split('@')[0]) || 'Account'
})

const helloText = computed(
  () =>
    (t('auth.hello', { name: displayName.value }) as string) ||
    `Hello ${displayName.value || 'Account'}`
)

onMounted(async () => {
  try {
    const token = localStorage.getItem('pk:token') || localStorage.getItem('auth_token')
    if (token && auth && !user.value && typeof auth.reloadUser === 'function') {
      await auth.reloadUser()
    }
  } catch {}
})

/* Locale dir (fully guarded) */
const currentLocale = computed(
  () =>
    safeLocales.value.find(l => l.code === localeCode.value) ??
    { code: localeCode.value, dir: 'ltr' }
)

const dir = computed(() => currentLocale.value?.dir || 'ltr')
useHead({ htmlAttrs: { lang: () => localeCode.value, dir: () => dir.value } })

// Redirect on locale change
if (process.client) {
  watch(
    () => localeCode.value,
    (n, o) => {
      if (n && n !== o) {
        navigateTo(switchLocalePath(n))
      }
    }
  )
}

/* Cart (guard nulls & SSR) */
const cartItems = computed<any[]>(() => {
  const itemsRef = cart?.items
  return Array.isArray(itemsRef?.value) ? itemsRef.value : []
})
const cartCount = computed(() =>
  cartItems.value.reduce((n, i: any) => n + Number(i?.qty || 0), 0)
)

/* Mobile drawer */
const mobileOpen = ref(false)

/* Nav helpers */
const go = (p: string) => navigateTo(localePath(p))

async function doLogout() {
  try {
    await auth?.logout?.()
  } finally {
    try {
      await cart?.clear?.()
    } catch {}
    go('/')
  }
}
</script>

<template>
  <header class="sticky top-0 z-40 border-b border-gray-200 bg-white/90 backdrop-blur">
    <div class="mx-auto w-full max-w-6xl px-4">
      <!-- Desktop: single row -->
      <HeaderDesktop
        class="hidden md:flex"
        :is-logged-in="isLoggedIn"
        :hello-text="helloText"
        :cart-count="cartCount"
        :locale="localeCode"
        :t="t"
        @home="go('/')"
        @kia-before="go('/services/kia-hyundai-before-2017')"
        @kia-after="go('/services/kia-hyundai-after-2017')"
        @login="go('/login')"
        @account="go('/account')"
        @cart="go('/cart')"
        @update:locale="(v: string) => setLocale(v)"
      />

      <!-- Mobile -->
      <HeaderMobile
        class="md:hidden"
        :is-logged-in="isLoggedIn"
        :hello-text="helloText"
        :cart-count="cartCount"
        :locale="localeCode"
        :t="t"
        :open="mobileOpen"
        @toggle="mobileOpen = !mobileOpen"
        @home="go('/')"
        @categories="go('/categories')"
        @lookups="go('/lookups')"
        @login="go('/login')"
        @account="go('/account')"
        @cart="go('/cart')"
      />
    </div>

    <!-- Mobile drawer -->
    <Transition name="slide-fade">
      <div v-if="mobileOpen" class="md:hidden border-t border-gray-200">
        <div class="mx-auto max-w-6xl px-4 py-3 flex flex-col gap-2">
          <button
            class="px-3 py-2 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50 text-left"
            @click="mobileOpen = false; go('/categories')"
          >
            {{ t('nav.categories') || 'Categories' }}
          </button>
          <button
            class="px-3 py-2 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50 text-left"
            @click="mobileOpen = false; go('/lookups')"
          >
            {{ t('nav.lookups') || 'My Lookups' }}
          </button>

          <!-- Language select (NO v-model) -->
          <div class="flex items-center gap-2 pt-2">
            <label class="text-sm text-gray-600">
              {{ t('common.language') || 'Language' }}
            </label>
            <select
              :value="localeCode"
              class="border border-gray-200 rounded-lg px-2 py-1 text-sm bg-white hover:border-brand transition"
              @change="(e: any) => setLocale(e.target.value)"
            >
              <option value="en">EN</option>
              <option value="ar">العربية</option>
            </select>
          </div>

          <div class="pt-2">
            <button v-if="isLoggedIn" class="btn w-full" @click="mobileOpen = false; go('/account')">
              {{ helloText }}
            </button>
            <button v-else class="btn w-full" @click="go('/login')">
              {{ t('auth.login') || 'Login' }}
            </button>
          </div>

          <div v-if="isLoggedIn" class="pt-1">
            <button class="btn-ghost w-full" @click="mobileOpen = false; doLogout()">
              {{ t('auth.logout') || 'Logout' }}
            </button>
          </div>
        </div>
      </div>
    </Transition>
  </header>
</template>

<style scoped>
.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: all 0.18s ease;
}
.slide-fade-enter-from,
.slide-fade-leave-to {
  opacity: 0;
  transform: translateY(-6px);
}
.btn {
  @apply inline-flex items-center justify-center rounded-xl bg-orange-500 text-white px-4 py-2 hover:bg-orange-600 transition;
}
.btn-ghost {
  @apply inline-flex items-center justify-center rounded-xl bg-white text-orange-600 border border-orange-200 px-4 py-2 hover:bg-orange-50 transition;
}
</style>
