<script setup lang="ts">
const props = defineProps<{
  isLoggedIn: boolean
  helloText: string
  cartCount: number
  locale: any              // can be ref or string
  t: (key: string, params?: any) => string
}>()

const emit = defineEmits([
  'home',
  'kia-before',
  'kia-after',
  'login',
  'account',
  'cart',
  'update:locale'
])

// Always work with a plain string for the <select>
const currentLocale = computed<string>(() =>
  typeof props.locale === 'object' &&
  props.locale !== null &&
  'value' in props.locale
    ? (props.locale as any).value
    : (props.locale as string)
)

function onChangeLocale(e: Event) {
  const value = (e.target as HTMLSelectElement).value
  emit('update:locale', value)
}
</script>

<template>
  <div class="h-14 flex items-center gap-4">
    <!-- Left: Brand -->
    <button class="flex items-center gap-2 group shrink-0" @click="emit('home')">
      <div class="h-9 w-9 rounded-xl bg-brand/10 grid place-items-center text-brand font-bold">
        PK
      </div>
      <span class="font-semibold text-gray-800 group-hover:text-brand transition">
        PIN Code
      </span>
    </button>

    <!-- Center: Nav (2 Kia/Hyundai items) -->
    <nav class="ml-6 flex items-center gap-1">
      <button
        class="px-3 py-2 text-sm font-medium text-gray-700 hover:text-brand transition"
        @click="emit('kia-before')"
      >
        {{ props.t('nav.kiaBefore2017') || 'Kia / Hyundai VIN to PIN (before 2017)' }}
      </button>

      <button
        class="px-3 py-2 text-sm font-medium text-gray-700 hover:text-brand transition"
        @click="emit('kia-after')"
      >
        {{ props.t('nav.kiaAfter2017') || 'Kia / Hyundai VIN to PIN (2017 and after)' }}
      </button>

      <!-- Language select -->
      <select
        :value="currentLocale"
        class="ml-1 border border-gray-200 rounded-lg px-2 py-1 text-sm bg-white hover:border-brand cursor-pointer transition"
        @change="onChangeLocale"
      >
        <option value="en">EN</option>
        <option value="ar">العربية</option>
      </select>
    </nav>

    <!-- Spacer -->
    <div class="flex-1"></div>

    <!-- Right actions -->
    <div class="flex items-center gap-3">
      <button
        v-if="props.isLoggedIn"
        class="px-3 py-2 text-sm font-medium text-gray-700 hover:text-brand transition"
        @click="emit('account')"
      >
        {{ props.helloText }}
      </button>
      <button
        v-else
        class="px-3 py-2 text-sm font-medium text-gray-700 hover:text-brand transition"
        @click="emit('login')"
      >
        {{ props.t('auth.login') || 'Login' }}
      </button>

      <button
        class="relative inline-flex h-9 w-9 items-center justify-center rounded-lg border border-gray-200 bg-white text-gray-700 hover:bg-gray-50"
        aria-label="Cart"
        @click="emit('cart')"
      >
        <svg
          class="h-[18px] w-[18px]"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <circle cx="9" cy="21" r="1"></circle>
          <circle cx="20" cy="21" r="1"></circle>
          <path d="M1 1h4l2.2 10.9a2 2 0 0 0 2 1.6h9.7a2 2 0 0 0 2-1.6L23 6H6"></path>
        </svg>
        <span
          v-if="props.cartCount > 0"
          class="absolute -top-1 -right-1 min-w-[18px] h-[18px] px-1 rounded-full text-[11px] grid place-items-center bg-red-600 text-white"
        >
          {{ props.cartCount }}
        </span>
      </button>
    </div>
  </div>
</template>
